import React, { useState } from 'react'
// import { makeStyles } from '@material-ui/styles'
import { QrCodeReader } from 'components'
import Notifier, { openSnackbar } from 'components/Notifier';
import { useTitle } from 'utils'
import { Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  card: {
    position: 'relative',
    height: 512,
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: (theme.palette.type === 'dark') ? '#333' : '#fff',
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  pdfWrapper: {
    width: '95%',
    height: '96vh',
  },
  button: {
    margin: theme.spacing.unit,
  },
}))

function AvaliarTrabalho() {
  const classes = useStyles()
  const [resfreshDummy, setResfreshDummy] = useState(false)
  useTitle('Rate Papers| PSE-2019');

  const handleError = err => {
    console.error('Rate Papers', err)
    const result = 'Error in device identification. You can rate in /works'
    openSnackbar({ message: result, variant: 'error' })
  }

  const refreshPage = () => {
    // window.location.reload()
    console.log(resfreshDummy)
    setResfreshDummy(older => !older)
  }

  return (
    <div className={classes.container}>
      <QrCodeReader
        handleError={handleError}>
      </QrCodeReader>
      <Button variant="contained" className={classes.button}
        onClick={refreshPage}
      >
        Refresh Scan
      </Button>
      <Notifier></Notifier>
    </div>
  )
}

export default AvaliarTrabalho
